.fuhsa-1-icon {
  position: absolute;
  top: 0;
  left: calc(50% - 124px);
  width: 248px;
  height: 50px;
  object-fit: cover;
}
.fuhsa-1-wrapper {
  align-self: stretch;
  position: relative;
  height: 50px;
}
.ok-icon {
  position: relative;
  width: 96px;
  height: 96px;
  object-fit: cover;
}
.ok-wrapper {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  padding: 38px 210px;
  align-items: center;
  justify-content: center;
}
.congratulations {
  position: relative;
  display: inline-block;
  width: 347px;
  flex-shrink: 0;
}
.congratulations-wrapper {
  overflow: hidden;
  display: flex;
  flex-direction: row;
  padding: var(--padding-mini) 88px;
  align-items: center;
  justify-content: center;
}
.we-will-keep {
  position: relative;
  line-height: 150%;
  display: inline-block;
  width: 210px;
  flex-shrink: 0;
}
.we-will-keep-in-touch-wrapper {
  overflow: hidden;
  display: flex;
  flex-direction: row;
  padding: var(--padding-3xs) 157px;
  align-items: center;
  justify-content: center;
  font-size: var(--p1-regular-16-size);
  color: var(--logo-black);
}
.close {
  position: relative;
  font-size: var(--p1-regular-16-size);
  line-height: 150%;
  font-family: var(--p1-regular-16);
  color: var(--gray-1);
  text-align: center;
}
.buttonxxlarge,
.buttonxxlarge-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}
.buttonxxlarge {
  cursor: pointer;
  border: 0;
  padding: var(--padding-5xs) 27px;
  background-color: var(--royal-blue);
  border-radius: var(--br-7xs);
  width: 400px;
  height: 44px;
  flex-direction: row;
  box-sizing: border-box;
}
.buttonxxlarge-wrapper {
  flex-direction: column;
  padding: var(--padding-xl) 62px;
}
.buttonxxlarge-wrapper,
.frame-child {
  align-self: stretch;
  overflow: hidden;
}
.frame-parent,
.welcome-page {
  background-color: var(--white);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.frame-parent {
  border-radius: var(--br-5xs);
  width: 524px;
  height: 521px;
}
.welcome-page {
  position: relative;
  width: 100%;
  overflow: hidden;
  padding: var(--padding-31xl) var(--padding-162xl);
  box-sizing: border-box;
  gap: 13px;
  text-align: center;
  font-size: var(--h2-bold-32-size);
  color: var(--royal-blue);
  font-family: var(--p1-regular-16);
}
@media screen and (max-width: 420px) {
  .ok-wrapper {
    padding-top: 35px;
    padding-bottom: 25px;
    box-sizing: border-box;
  }
  .congratulations {
    font-size: 22px;
    text-align: center;
  }
  .congratulations-wrapper {
    padding-top: var(--padding-3xs);
    padding-bottom: var(--padding-3xs);
    box-sizing: border-box;
  }
  .we-will-keep {
    font-size: 11px;
  }
  .we-will-keep-in-touch-wrapper {
    padding-top: var(--padding-3xs);
    padding-bottom: var(--padding-3xs);
    box-sizing: border-box;
  }
  .close {
    font-size: var(--font-size-sm);
  }
  .buttonxxlarge {
    width: 270px;
    height: 40px;
  }
  .buttonxxlarge-wrapper {
    padding-top: var(--padding-11xl);
    padding-bottom: var(--padding-xl);
    box-sizing: border-box;
  }
  .frame-child {
    width: 464px;
    padding-left: 180px;
    padding-top: 9px;
    padding-right: 0;
    box-sizing: border-box;
  }
  .frame-parent {
    width: 350px;
    height: 380px;
  }
}
@media screen and (max-width: 350px) {
  .ok-wrapper {
    padding-top: 0;
    padding-bottom: 0;
    box-sizing: border-box;
  }
  .congratulations {
    font-size: 20px;
  }
  .congratulations-wrapper {
    padding-top: var(--padding-mini);
    padding-bottom: 0;
    box-sizing: border-box;
  }
  .we-will-keep {
    font-size: 12px;
  }
  .we-will-keep-in-touch-wrapper {
    padding-top: 0;
    padding-bottom: 0;
    box-sizing: border-box;
    margin-top: 20px;
  }
  .close {
    font-size: var(--font-size-sm);
  }
  .buttonxxlarge {
    width: 200px;
  }
  .frame-child {
    padding-left: var(--padding-3xs);
    padding-right: var(--padding-3xs);
    padding-bottom: 17px;
    box-sizing: border-box;
    justify-content: center;
  }
  .frame-parent {
    width: 290px;
    height: 450px;
    align-items: center;
    justify-content: center;
  }
}
