@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");
@import url("https://fonts.googleapis.com/icon?family=Material+Icons");

body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --p1-regular-16: Poppins;

  /* font sizes */
  --p1-regular-16-size: 16px;
  --font-size-sm: 14px;
  --h2-bold-32-size: 32px;
  --font-size-5xl: 24px;
  --font-size-3xs: 10px;
  --font-size-smi: 13px;
  --font-size-mini: 15px;

  /* Colors */
  --white: #fff;
  --royal-blue: #0d0ed9;
  --color-whitesmoke-100: #f4f2f2;
  --gray-1: #f2f2f2;
  --color-gray-100: #282626;
  --logo-black: #010101;
  --color-firebrick: #b50909;
  --color-black: #000;
  --color-royalblue: #1054d7;
  --color-silver: #c2bbbb;

  /* Gaps */
  --gap-69xl: 88px;

  /* Paddings */
  --padding-31xl: 50px;
  --padding-162xl: 181px;
  --padding-3xs: 10px;
  --padding-xl: 20px;
  --padding-11xl: 30px;
  --padding-5xs: 8px;
  --padding-mini: 15px;
  --padding-50xl: 69px;
  --padding-30xl: 49px;
  --padding-52xl: 71px;
  --padding-6xs: 7px;
  --padding-53xl: 72px;
  --padding-xs: 12px;

  /* border radiuses */
  --br-5xs: 8px;
  --br-7xs: 6px;
  --br-10xs: 3px;
  --br-2xs: 11px;

  /* Effects */
  --shadow-1: 0px 1px 3px rgba(0, 0, 0, 0.25);
}
