.fuhsa-1-icon1 {
  position: absolute;
  top: 0;
  left: calc(50% - 124px);
  width: 248px;
  height: 50px;
  object-fit: cover;
}
.fuhsa-1-container {
  position: relative;
  width: 1142px;
  height: 50px;
}
.admin-assistant {
  position: relative;
}
.admin-assistant-wrapper,
.inputtextboxno-label {
  width: 545px;
  display: flex;
  flex-direction: column;
  padding: 28px 0 var(--padding-6xs);
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
}
.inputtextboxno-label {
  border: 0;
  font-family: var(--p1-regular-16);
  font-size: var(--p1-regular-16-size);
  background-color: var(--white);
  border-radius: var(--br-7xs);
  box-shadow: var(--shadow-1);
  width: 400.63px;
  height: 44px;
  overflow: hidden;
  flex-shrink: 0;
  flex-direction: row;
  padding: var(--padding-3xs) var(--padding-xs);
  align-items: flex-start;
  justify-content: flex-start;
}
.inputtextboxno-label-wrapper {
  width: 543px;
  padding: var(--padding-3xs) 67px;
  box-sizing: border-box;
}
.frame-div,
.inputtextboxno-label-container,
.inputtextboxno-label-frame,
.inputtextboxno-label-wrapper {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.inputtextboxno-label-container {
  width: 545px;
  padding: var(--padding-3xs) var(--padding-53xl);
  box-sizing: border-box;
}
.frame-div,
.inputtextboxno-label-frame {
  padding: 6px var(--padding-53xl);
}
.frame-div {
  padding: var(--padding-6xs) var(--padding-53xl);
}
.upload-cv {
  position: relative;
  font-size: var(--p1-regular-16-size);
  line-height: 150%;
  font-family: var(--p1-regular-16);
  color: var(--gray-1);
  text-align: left;
}
.buttonxxlarge1 {
  cursor: pointer;
  border: 0;
  padding: var(--padding-5xs) var(--padding-50xl);
  background-color: var(--color-firebrick);
  border-radius: var(--br-7xs);
  width: 401px;
  height: 46px;
  flex-direction: row;
  box-sizing: border-box;
}
.buttonxxlarge-container,
.buttonxxlarge1,
.buttonxxlarge2 {
  display: flex;
  align-items: center;
  justify-content: center;
}
.buttonxxlarge-container {
  overflow: hidden;
  flex-direction: column;
  padding: var(--padding-5xs) var(--padding-52xl);
}
.buttonxxlarge2 {
  cursor: pointer;
  border: 0;
  padding: var(--padding-5xs) var(--padding-50xl);
  background-color: var(--royal-blue);
  border-radius: var(--br-7xs);
  width: 401px;
  height: 46px;
  flex-direction: row;
  box-sizing: border-box;
}
.group-child {
  position: absolute;
  top: 0;
  left: 0;
  width: 545px;
  overflow: hidden;
}
.create-your-account-inner {
  position: relative;
  width: 545px;
  height: 70px;
}
.apply,
.create-your-account {
  background-color: var(--white);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.create-your-account {
  border-radius: var(--br-5xs);
}
.apply {
  position: relative;
  width: 100%;
  padding: 53px var(--padding-50xl);
  box-sizing: border-box;
  gap: 49px;
  text-align: left;
  font-size: var(--font-size-5xl);
  color: var(--royal-blue);
  font-family: var(--p1-regular-16);
}
@media screen and (max-width: 420px) {
  .admin-assistant {
    font-size: 17px;
    text-align: center;
  }
  .inputtextboxno-label {
    width: 270px;
  }
  .upload-cv {
    font-size: var(--p1-regular-16-size);
    text-align: center;
    width: 246px;
  }
  .buttonxxlarge1,
  .buttonxxlarge2 {
    width: 270px;
  }
  .group-child {
    padding-top: 5px;
    box-sizing: border-box;
  }
  .create-your-account {
    width: 90%;
  }
  .apply {
    padding-left: var(--padding-30xl);
    padding-right: var(--padding-30xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 350px) {
  .buttonxxlarge1,
  .buttonxxlarge2,
  .inputtextboxno-label {
    width: 240px;
  }
}
