.fuhsa-1-icon2 {
  position: relative;
  width: 248px;
  height: 50px;
  object-fit: cover;
}
.fuhsa-1-frame {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  padding: 0 447px;
  align-items: center;
  justify-content: center;
}
.admin-assistant1 {
  position: relative;
  font-weight: 600;
  display: inline-block;
  width: 201px;
  flex-shrink: 0;
}
.admin-assistant-container {
  width: 1256px;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  padding: 0 var(--padding-162xl);
  box-sizing: border-box;
  align-items: flex-start;
  justify-content: flex-start;
  font-size: var(--font-size-5xl);
}
.federal-university-of {
  margin: 0;
}
.federal-university-of-container {
  position: relative;
  font-weight: 600;
}
.federal-university-of-heath-an-wrapper {
  width: 1265px;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  padding: var(--padding-3xs) 185px;
  box-sizing: border-box;
  align-items: flex-start;
  justify-content: flex-start;
  color: #8f8f8f;
}
.apply-child {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 10px;
  background-color: var(--color-royalblue);
  width: 301px;
  height: 46px;
}
.apply-now {
  position: absolute;
  top: 12px;
  left: 97px;
  font-weight: 600;
}
.apply2 {
  position: relative;
  width: 301px;
  height: 46px;
  cursor: pointer;
}
.application-deadline-240320-wrapper,
.apply-wrapper {
  overflow: hidden;
  display: flex;
  box-sizing: border-box;
  align-items: flex-start;
  justify-content: flex-start;
}
.apply-wrapper {
  width: 1296px;
  flex-direction: column;
  padding: 1px var(--padding-162xl);
  color: var(--white);
}
.application-deadline-240320-wrapper {
  width: 1289px;
  flex-direction: row;
  padding: 4px var(--padding-162xl);
  font-size: var(--font-size-3xs);
  color: #730e0e;
}
.job-details {
  position: relative;
  font-weight: 500;
}
.job-details-wrapper {
  width: 1295px;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  padding: 0 var(--padding-162xl);
  box-sizing: border-box;
  align-items: flex-start;
  justify-content: flex-start;
  font-size: var(--font-size-5xl);
}
.job-type {
  position: relative;
  font-weight: 600;
}
.job-type-wrapper {
  width: 1295px;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  padding: 13px var(--padding-162xl);
  box-sizing: border-box;
  align-items: flex-start;
  justify-content: flex-start;
}
.full-time {
  position: relative;
}
.full-time-wrapper {
  width: 1281px;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  padding: 0 var(--padding-162xl);
  box-sizing: border-box;
  align-items: flex-start;
  justify-content: flex-start;
  font-size: var(--font-size-smi);
}
.full-job-description {
  position: relative;
}
.full-job-description-wrapper {
  width: 1282px;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  padding: 0 var(--padding-162xl);
  box-sizing: border-box;
  align-items: flex-start;
  justify-content: flex-start;
}
.ac-partner-content-container {
  position: relative;
  display: inline-block;
  width: 662px;
  flex-shrink: 0;
}
.ac-partner-content-coordinati-wrapper {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  padding: var(--padding-3xs) 282px var(--padding-3xs) var(--padding-3xs);
  align-items: flex-start;
  justify-content: flex-start;
}
.details,
.details-inner {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.details-inner {
  align-self: stretch;
  padding: 0 var(--padding-162xl);
  font-size: var(--font-size-3xs);
}
.details {
  position: relative;
  background-color: var(--white);
  width: 100%;
  padding: 14px 0;
  box-sizing: border-box;
  text-align: left;
  font-size: var(--p1-regular-16-size);
  color: var(--color-black);
  font-family: var(--p1-regular-16);
}
@media screen and (max-width: 420px) {
  .fuhsa-1-frame {
    padding-left: 200px;
    box-sizing: border-box;
    margin-bottom: 30px;
  }
  .admin-assistant1 {
    font-size: var(--p1-regular-16-size);
  }
  .admin-assistant-container {
    padding-left: var(--padding-11xl);
    box-sizing: border-box;
  }
  .federal-university-of-container {
    font-size: var(--font-size-smi);
  }
  .federal-university-of-heath-an-wrapper {
    padding-left: var(--padding-11xl);
    box-sizing: border-box;
  }
  .apply-child {
    height: 40px;
  }
  .apply-now {
    font-size: var(--font-size-sm);
  }
  .application-deadline-240320-wrapper,
  .apply-wrapper {
    padding-left: var(--padding-11xl);
    box-sizing: border-box;
  }
  .job-details {
    font-size: 18px;
  }
  .job-details-wrapper {
    padding-left: var(--padding-11xl);
    box-sizing: border-box;
  }
  .job-type {
    font-size: var(--font-size-sm);
  }
  .full-time-wrapper,
  .job-type-wrapper {
    padding-left: var(--padding-11xl);
    box-sizing: border-box;
  }
  .full-job-description {
    font-size: var(--font-size-sm);
  }
  .full-job-description-wrapper {
    padding-left: var(--padding-11xl);
    box-sizing: border-box;
  }
  .ac-partner-content-container {
    font-size: 8px;
  }
  .details-inner {
    padding-left: var(--padding-11xl);
    box-sizing: border-box;
  }
}
